import { Inject, System } from 'flat-ecs';
import {
  Animation,
  createAnimation,
  createBatch,
  createWhiteTexture,
  PlayMode,
  SpriteBatch,
  Texture,
  Viewport
} from 'gdxjs';
import AssetManager from '../AssetManager';
import Constants from '../Constants';
import PlayerState from '../PlayerState';
import Star from '../Star';

export default class StarRenderSystem extends System {
  @Inject('viewport')
  viewport: Viewport;
  @Inject('context')
  gl: WebGLRenderingContext;
  @Inject('stars')
  stars: Array<Star>;
  @Inject('assetManager')
  assetManager: AssetManager;
  @Inject('playerState')
  playerState: PlayerState;

  batch: SpriteBatch;
  whiteTex: Texture;
  starAnimation: Animation;
  specialAnimations: Animation[];

  initialized() {
    this.batch = createBatch(this.gl);
    this.whiteTex = createWhiteTexture(this.gl);
    const atlas = this.assetManager.getAtlas('./object.atlas');
    if (atlas) {
      this.starAnimation = createAnimation(0.2, atlas.findRegions('star'));
      this.specialAnimations = ['hand', 'hand', 'feet', 'feet', 'chest', 'head'].map(s =>
        createAnimation(0.2, atlas.findRegions(s))
      );
    }
  }
  elapsed = 0;
  process(): void {
    this.batch.setProjection(this.viewport.getCamera().combined);
    this.batch.begin();
    this.elapsed += this.world.delta;
    for (let star of this.stars) {
      const { x, y, active, special } = star;
      if (!active) continue;
      if (special) {
        this.specialAnimations[this.playerState.getCurrentStateIndex()]
          .getKeyFrame(this.elapsed, PlayMode.LOOP)
          .draw(
            this.batch,
            x - Constants.STAR_SIZE / 2,
            y - Constants.STAR_SIZE / 2,
            Constants.STAR_SIZE,
            Constants.STAR_SIZE,
            Constants.STAR_SIZE / 2,
            Constants.STAR_SIZE / 2,
            0,
            2,
            2
          );
      } else {
        this.starAnimation
          .getKeyFrame(this.elapsed, PlayMode.LOOP)
          .draw(
            this.batch,
            x - Constants.STAR_SIZE / 2,
            y - Constants.STAR_SIZE / 2,
            Constants.STAR_SIZE,
            Constants.STAR_SIZE
          );
      }
    }
    this.batch.end();
  }
}
