import { Inject, System } from 'flat-ecs';
import { Animation, createAnimation, createBatch, OrthographicCamera, PlayMode, SpriteBatch } from 'gdxjs';
import Alien from '../Alien';
import AssetManager from '../AssetManager';
import Constants from '../Constants';

export default class AlienRenderSystem extends System {
  @Inject('aliens')
  aliens: Array<Alien>;
  @Inject('fixedCamera')
  fixedCamera: OrthographicCamera;
  @Inject('context')
  gl: WebGLRenderingContext;
  @Inject('assetManager')
  assetManager: AssetManager;

  batch: SpriteBatch;

  animations: Animation[] = [];

  initialized() {
    this.batch = createBatch(this.gl);

    const atlas = this.assetManager.getAtlas('./main-char.atlas');
    if (atlas) {
      ['alien1', 'alien2', 'alien3'].forEach(s => this.animations.push(createAnimation(0.4, atlas.findRegions(s))));
    }
  }

  elapsed = 0;
  process(): void {
    this.batch.setProjection(this.fixedCamera.combined);
    this.batch.begin();
    this.elapsed += this.world.delta;
    for (let alien of this.aliens) {
      this.animations[alien.type]
        .getKeyFrame(Constants.ALIEN_SPAWNING_TIME - alien.time, PlayMode.NORMAL)
        .draw(this.batch, alien.x, alien.y, Constants.ALIEN_WIDTH, Constants.ALIEN_HEIGHT);
    }
    this.batch.end();
  }
}
