import { Inject, System } from 'flat-ecs';
import { createBatch, OrthographicCamera, SpriteBatch, Texture, Vector2 } from 'gdxjs';
import AssetManager from '../AssetManager';
import Constants from '../Constants';

export default class BackgroundRenderSystem extends System {
  @Inject('context')
  gl: WebGLRenderingContext;
  @Inject('assetManager')
  assetManager: AssetManager;
  @Inject('fixedCamera')
  fixedCamera: OrthographicCamera;
  @Inject('playerPosition')
  playerPos: Vector2;

  batch: SpriteBatch;
  textures: Map<string, Texture | undefined> = new Map();
  initialized() {
    this.batch = createBatch(this.gl);
    ['bg', 'city_01', 'city_02', 'cloud_01', 'cloud_02', 'smoke_01', 'smoke_02'].map(s =>
      this.textures.set(s, this.assetManager.getTexture(`./background/${s}.png`))
    );
  }
  drawBackground() {
    const bg = this.textures.get('bg') as Texture;
    const drawHeight = Constants.CAMERA_HEIGHT;
    const drawWidth = (drawHeight / bg.height) * bg.width;
    let i = 0;
    while (i * drawWidth < Constants.CAMERA_WIDTH) {
      this.batch.draw(bg, i * drawWidth, 0, drawWidth, drawHeight);
      i++;
    }
  }
  process(): void {
    const offset = -this.playerPos.x;
    this.batch.setProjection(this.fixedCamera.combined);
    this.batch.begin();
    this.drawBackground();
    this.drawCloud1(offset * 0.5);
    this.drawCloud2(offset * 0.8);
    this.drawCity1(offset);
    this.drawCity2(offset * 1.1);
    this.batch.end();
  }
  drawCity1(offset: number) {
    const city = this.textures.get('city_01') as Texture;
    const drawHeight = Constants.CAMERA_HEIGHT / 1.5;
    const drawWidth = (drawHeight / city.height) * city.width;
    while (offset <= 0) {
      offset += drawWidth;
    }
    this.batch.draw(city, offset - drawWidth, Constants.CAMERA_HEIGHT - drawHeight, drawWidth, drawHeight);
    while (offset <= Constants.CAMERA_WIDTH) {
      this.batch.draw(city, offset, Constants.CAMERA_HEIGHT - drawHeight, drawWidth, drawHeight);
      offset += drawWidth;
    }
  }
  drawCity2(offset: number) {
    const city = this.textures.get('city_02') as Texture;
    const drawHeight = Constants.CAMERA_HEIGHT / 1.5;
    const drawWidth = (drawHeight / city.height) * city.width;
    while (offset <= 0) {
      offset += drawWidth;
    }
    this.batch.draw(city, offset - drawWidth, Constants.CAMERA_HEIGHT - drawHeight, drawWidth, drawHeight);
    while (offset <= Constants.CAMERA_WIDTH) {
      this.batch.draw(city, offset, Constants.CAMERA_HEIGHT - drawHeight, drawWidth, drawHeight);
      offset += drawWidth;
    }
  }
  drawCloud1(offset: number) {
    const cloud = this.textures.get('cloud_01') as Texture;
    const drawHeight = Constants.CAMERA_HEIGHT / 1.2;
    const drawWidth = (drawHeight / cloud.height) * cloud.width;
    const gap = drawWidth;
    while (offset <= 0) {
      offset += drawWidth + gap;
    }
    this.batch.draw(cloud, offset - drawWidth, Constants.CAMERA_HEIGHT - drawHeight, drawWidth, drawHeight);
    while (offset + gap <= Constants.CAMERA_WIDTH) {
      this.batch.draw(cloud, offset + gap, Constants.CAMERA_HEIGHT - drawHeight, drawWidth, drawHeight);
      offset += drawWidth + gap;
    }
  }
  drawCloud2(offset: number) {
    const cloud = this.textures.get('cloud_02') as Texture;
    const drawHeight = Constants.CAMERA_HEIGHT / 6;
    const drawWidth = (drawHeight / cloud.height) * cloud.width;
    const gap = drawWidth;
    while (offset <= 0) {
      offset += drawWidth + gap;
    }
    this.batch.draw(cloud, offset - drawWidth, Constants.CAMERA_HEIGHT / 4, drawWidth, drawHeight);
    while (offset + gap <= Constants.CAMERA_WIDTH) {
      this.batch.draw(cloud, offset + gap, Constants.CAMERA_HEIGHT / 4, drawWidth, drawHeight);
      offset += drawWidth + gap;
    }
  }
}
