import {
  b2Body,
  b2BodyDef,
  b2BodyType,
  b2CircleShape,
  b2Contact,
  b2ContactListener,
  b2FixtureDef,
  b2PolygonShape,
  b2World
} from 'box2d.ts';
import { EventEmitter } from 'fbemitter';
import { Inject, System } from 'flat-ecs';
import { Vector2 } from 'gdxjs';
import { ViewportAwareInputHandler } from 'gdxjs/lib/createViewportAwareInputHandler';
import Constants from '../Constants';
import PlayerState from '../PlayerState';

export default class PlayerMovementSystem extends System {
  @Inject('physicsWorld')
  physicsWorld: b2World;
  @Inject('playerPosition')
  playerPos: Vector2;
  @Inject('inputHandler')
  inputHandler: ViewportAwareInputHandler;
  @Inject('playerState')
  playerState: PlayerState;
  @Inject('eventEmitter')
  eventEmitter: EventEmitter;
  playerBody: b2Body;
  initialized() {
    const bodyDef = new b2BodyDef();
    bodyDef.type = b2BodyType.b2_dynamicBody;
    bodyDef.fixedRotation = true;
    bodyDef.position.Set(2, Constants.CAMERA_HEIGHT / 2);
    const body = this.physicsWorld.CreateBody(bodyDef);
    body.SetUserData({
      type: 'user'
    });
    const shape = new b2CircleShape(Constants.PLAYER_WIDTH);
    shape.Set({ x: -Constants.PLAYER_WIDTH / 2, y: -Constants.PLAYER_HEIGHT / 2 }, Constants.PLAYER_WIDTH);
    body.CreateFixture(shape);

    const sensorShape = new b2PolygonShape();
    sensorShape.SetAsBox(Constants.PLAYER_WIDTH / 8, Constants.PLAYER_WIDTH / 8, {
      x: 0,
      y: Constants.PLAYER_HEIGHT / 2
    });
    const fixDef = new b2FixtureDef();
    fixDef.isSensor = true;
    fixDef.shape = sensorShape;
    fixDef.userData = { type: 'playerSensor' };

    body.CreateFixture(fixDef);

    const sensorShape2 = new b2PolygonShape();
    sensorShape2.SetAsBox(Constants.PLAYER_WIDTH / 8, Constants.PLAYER_WIDTH / 8, {
      x: Constants.PLAYER_WIDTH / 2,
      y: 0
    });
    const fixDef2 = new b2FixtureDef();
    fixDef2.isSensor = true;
    fixDef2.shape = sensorShape2;
    fixDef2.userData = { type: 'playerSensor2' };

    body.CreateFixture(fixDef2);

    this.playerBody = body;

    class ContactListener extends b2ContactListener {
      movementSytem: PlayerMovementSystem;
      constructor(movementSystem: PlayerMovementSystem) {
        super();
        this.movementSytem = movementSystem;
      }
      processJumpable(contact: b2Contact): void {
        let otherFixture;

        const fixA = contact.GetFixtureA();
        const fixB = contact.GetFixtureB();

        if (fixA.IsSensor() && fixA.GetUserData()?.type === 'playerSensor') {
          otherFixture = fixB;
        } else if (fixB.IsSensor() && fixB.GetUserData()?.type === 'playerSensor') {
          otherFixture = fixA;
        }

        if (otherFixture && otherFixture.GetBody().GetUserData()?.type === 'block') {
          this.movementSytem.jumpLeft = 2;
          this.movementSytem.playerState.changeAction('run');
        }
      }
      processObstacle(contact: b2Contact): void {
        let otherFixture;

        const fixA = contact.GetFixtureA();
        const fixB = contact.GetFixtureB();

        if (fixA.IsSensor() && fixA.GetUserData()?.type === 'playerSensor2') {
          otherFixture = fixB;
        } else if (fixB.IsSensor() && fixB.GetUserData()?.type === 'playerSensor2') {
          otherFixture = fixA;
        }

        if (otherFixture && otherFixture.GetBody().GetUserData()?.type === 'block') {
          this.movementSytem.playerState.stunnedDuration = 0.2;
          this.movementSytem.playerState.changeAction('hurt');
        }
      }
      BeginContact(contact: b2Contact): void {
        if (this.movementSytem.gameOver) return;
        this.processJumpable(contact);
        this.processObstacle(contact);
        this.movementSytem.eventEmitter.emit('beginContact', contact);
      }
    }
    this.physicsWorld.SetContactListener(new ContactListener(this));
  }
  pressed = false;
  jumpLeft = 0;
  gameOver = false;
  zeroCenter = { x: 0, y: 0 };
  runImpulse = { x: Constants.RUN_FORCE, y: 0 };
  jumpImpulse = { x: 0, y: Constants.JUMP_FORCE };
  process(): void {
    if (this.playerState.stunnedDuration > 0) {
      if (!this.playerState.appliedStun) {
        this.playerState.appliedStun = true;
        this.playerBody.GetLinearVelocity().Set(0, 0);
        this.playerBody.ApplyLinearImpulse({ x: -2 * Constants.RUN_FORCE, y: 0 }, { x: 0, y: 0 });
      }
      this.playerState.stunnedDuration -= this.world.delta;
      if (this.playerState.stunnedDuration <= 0) {
        this.playerState.changeAction('run');
        this.playerState.appliedStun = false;
      }
    } else {
      const v = this.playerBody.GetLinearVelocity();
      v.Set(0, v.y);
      this.playerBody.ApplyLinearImpulse(this.runImpulse, this.zeroCenter);

      if (this.pressed) {
        if (!this.inputHandler.isTouched()) {
          this.pressed = false;
        }
      } else {
        if (this.inputHandler.isTouched() && this.jumpLeft > 0) {
          this.jumpLeft--;
          if (this.jumpLeft === 1) {
            this.playerState.changeAction('jump');
          } else {
            this.playerState.changeAction('doubleJump');
          }
          this.pressed = true;
          const v = this.playerBody.GetLinearVelocity();
          v.Set(v.x, 0);
          this.playerBody.ApplyLinearImpulse(this.jumpImpulse, this.zeroCenter);
        }
      }
    }
    const bodyPos = this.playerBody.GetPosition();
    this.playerPos.set(bodyPos.x, bodyPos.y);

    if (
      !this.gameOver &&
      (bodyPos.y > Constants.CAMERA_HEIGHT * 1.2 || this.playerState.hp <= 0 || this.playerState.complete)
    ) {
      this.gameOver = true;
      this.eventEmitter.emit('gameOver');
    }
  }
}
