import { Inject, System } from 'flat-ecs';
import {
  AlignMode,
  BitmapFont,
  createBatch,
  createWhiteTexture,
  OrthographicCamera,
  SpriteBatch,
  TextRenderer,
  Texture,
  TextureAtlas,
  TextureRegion
} from 'gdxjs';
import AssetManager from '../AssetManager';
import Constants from '../Constants';
import PlayerState from '../PlayerState';

const padNumber = (number: number, length: number): string => {
  let result = `${number}`;
  while (result.length < length) {
    result = `0${result}`;
  }
  return result;
};

export default class UISystem extends System {
  @Inject('fixedCamera')
  fixedCamera: OrthographicCamera;
  @Inject('context')
  gl: WebGLRenderingContext;
  @Inject('assetManager')
  assetManager: AssetManager;
  @Inject('playerState')
  playerState: PlayerState;
  batch: SpriteBatch;

  panel: TextureRegion;
  heartFull: TextureRegion;
  heartEmpty: TextureRegion;
  bar: TextureRegion;
  scoreLabel: TextureRegion;
  text: TextRenderer;
  whiteText: Texture;
  initialized() {
    this.batch = createBatch(this.gl);
    const atlas = this.assetManager.getAtlas('./ui.atlas') as TextureAtlas;
    this.panel = atlas.findRegions('panel')[0];
    this.heartFull = atlas.findRegions('heart-full')[0];
    this.heartEmpty = atlas.findRegions('heart-empty')[0];
    this.bar = atlas.findRegions('bar')[0];
    this.scoreLabel = atlas.findRegions('score-label')[0];
    const font = this.assetManager.getFont('./score.fnt') as BitmapFont;
    this.text = font.createRenderer(Constants.CAMERA_WIDTH * 0.35);
    this.text.setAlignMode(AlignMode.right);
    this.whiteText = createWhiteTexture(this.gl);
  }
  process(): void {
    this.batch.setProjection(this.fixedCamera.combined);
    this.batch.begin();

    const { width: panelWidth, height: panelHeight } = this.panel;
    const panelDrawWidth = Constants.CAMERA_WIDTH * 0.95;
    const panelDrawHeight = (panelDrawWidth / panelWidth) * panelHeight;
    const panelDrawX = Constants.CAMERA_WIDTH * 0.025;
    const panelDrawY = Constants.CAMERA_WIDTH * 0.05;
    this.panel.draw(this.batch, panelDrawX, panelDrawY, panelDrawWidth, panelDrawHeight);

    const { width: heartWidth, height: heartHeight } = this.heartFull;
    const heartDrawWidth = panelDrawWidth / 26;
    const heartDrawHeight = (heartDrawWidth / heartWidth) * heartHeight;
    const heartStartX = 2 * heartDrawWidth;
    const heartStartY = panelDrawY + panelDrawHeight - 1.75 * heartDrawHeight;
    const heartGap = heartDrawWidth * 0.2;
    for (let i = 0; i < 3; i++) {
      if (i < this.playerState.hp) {
        this.heartFull.draw(
          this.batch,
          heartStartX + (heartDrawWidth + heartGap) * i,
          heartStartY,
          heartDrawWidth,
          heartDrawHeight
        );
      } else {
        this.heartEmpty.draw(
          this.batch,
          heartStartX + (heartDrawWidth + heartGap) * i,
          heartStartY,
          heartDrawWidth,
          heartDrawHeight
        );
      }
    }

    const { width: barWidth, height: barHeight } = this.bar;
    const barDrawX = heartStartX;
    const barDrawWidth = panelDrawWidth - 2 * (barDrawX - panelDrawX);
    const barDrawHeight = (barDrawWidth / barWidth) * barHeight;
    const barDrawY = panelDrawY + panelDrawHeight / 1.7 - barDrawHeight;
    this.bar.draw(this.batch, barDrawX, barDrawY, barDrawWidth, barDrawHeight);

    const { width: scoreLabelWidth, height: scoreLabelHeight } = this.scoreLabel;
    const scoreDrawHeight = heartDrawHeight * 0.8;
    const scoreDrawWidth = (scoreDrawHeight / scoreLabelHeight) * scoreLabelWidth;
    const scoreDrawY = heartStartY + heartDrawHeight - scoreDrawHeight;
    const scoreDrawX = heartStartX + 10 * heartDrawWidth;
    this.scoreLabel.draw(this.batch, scoreDrawX, scoreDrawY, scoreDrawWidth, scoreDrawHeight);

    const fontSize = heartDrawHeight * 1.35;
    const textDrawY = scoreDrawY + scoreDrawHeight - fontSize;

    this.text.setDisplayWidth(panelDrawWidth - 3 * heartDrawWidth);
    this.text.draw(this.batch, padNumber(this.playerState.score, 10), panelDrawX, textDrawY, fontSize);

    const coverStartX = barDrawX;
    const coverGap = barDrawWidth / 250;
    const coverWidth = barDrawWidth / 6 - coverGap;
    const coverStartY = barDrawY + barDrawHeight;
    this.batch.setColor(0, 0, 0, 1);
    for (let i = 0; i < 6; i++) {
      if (i >= this.playerState.stateIndex) {
        const coverHeight = barDrawHeight * (1 - (5 - i) * 0.12);
        this.batch.draw(
          this.whiteText,
          coverStartX + i * (coverGap + coverWidth),
          coverStartY - coverHeight,
          coverWidth,
          coverHeight
        );
      }
    }
    this.batch.setColor(1, 1, 1, 1);
    this.batch.end();
  }
}
