import { Inject, System } from 'flat-ecs';
import { Vector2, Viewport } from 'gdxjs';
import Constants from '../Constants';

export default class CameraMovementSystem extends System {
  @Inject('viewport')
  viewport: Viewport;
  @Inject('playerPosition')
  playerPos: Vector2;
  process(): void {
    const camPos = this.viewport.getCamera().getPosition();
    this.viewport.getCamera().setPosition(this.playerPos.x - Constants.GAP, camPos.y);
  }
}
