import { b2World } from 'box2d.ts';
import { Inject, System } from 'flat-ecs';
import { Viewport } from 'gdxjs';
import Block from '../Block';
import Constants from '../Constants';
import Star from '../Star';

export default class BlockRemovalSystem extends System {
  @Inject('viewport')
  viewport: Viewport;
  @Inject('physicsWorld')
  physicsWorld: b2World;
  @Inject('blocks')
  blocks: Array<Block>;
  @Inject('stars')
  stars: Array<Star>;
  process(): void {
    const camPos = this.viewport.getCamera().getPosition();
    for (let i = this.blocks.length - 1; i >= 0; i--) {
      const block = this.blocks[i];
      if (camPos.x - block.x >= 3 * Constants.CAMERA_WIDTH) {
        this.physicsWorld.DestroyBody(block.body);
        this.blocks.splice(i, 1);
      }
    }
    for (let i = this.stars.length - 1; i >= 0; i--) {
      const star = this.stars[i];
      if (camPos.x - star.x >= 3 * Constants.CAMERA_WIDTH) {
        this.physicsWorld.DestroyBody(star.body);
        this.stars.splice(i, 1);
      }
    }
  }
}
