import { Inject, System } from 'flat-ecs';
import { createBatch, createWhiteTexture, SpriteBatch, Texture, TextureAtlas, TextureRegion, Viewport } from 'gdxjs';
import AssetManager from '../AssetManager';
import Block from '../Block';

export default class BlockRenderSystem extends System {
  @Inject('viewport')
  viewport: Viewport;
  @Inject('context')
  gl: WebGLRenderingContext;
  @Inject('blocks')
  blocks: Array<Block>;
  @Inject('assetManager')
  assetManager: AssetManager;

  batch: SpriteBatch;
  whiteTex: Texture;
  atlas: TextureAtlas;
  regions: TextureRegion[];
  initialized() {
    this.batch = createBatch(this.gl);
    this.whiteTex = createWhiteTexture(this.gl);
    const atlas = this.assetManager.getAtlas('./ground.atlas');
    if (atlas) {
      this.atlas = atlas;
      this.regions = atlas.findRegions('tile');
    }
  }
  process(): void {
    this.batch.setProjection(this.viewport.getCamera().combined);
    this.batch.begin();
    for (let box of this.blocks) {
      const { x, y, w, h, type } = box;
      this.regions[type].draw(this.batch, x - w / 2, y - h / 2, w, h);
    }
    this.batch.end();
  }
}
