import { EventEmitter } from 'events';
import { Inject, System } from 'flat-ecs';
import { Vector2 } from 'gdxjs';
import Alien from '../Alien';
import Constants from '../Constants';
import PlayerState from '../PlayerState';

export default class AlienSystem extends System {
  @Inject('aliens')
  aliens: Array<Alien>;
  @Inject('playerPosition')
  playerPos: Vector2;
  @Inject('playerState')
  playerState: PlayerState;
  @Inject('eventEmitter')
  eventEmitter: EventEmitter;

  process(): void {
    for (let i = this.aliens.length - 1; i >= 0; i--) {
      const alien = this.aliens[i];

      if (alien.time > 0) {
        alien.time -= this.world.delta;
        return;
      }

      if (!alien.hit) {
        const { y: py } = this.playerPos;
        if (alien.x <= Constants.GAP && alien.x + Constants.ALIEN_WIDTH >= Constants.GAP) {
          if (alien.y <= py && alien.y + Constants.ALIEN_HEIGHT >= py) {
            this.playerState.stunnedDuration = 0.3;
            this.playerState.changeAction('hurt');
            this.playerState.hp--;
            alien.hit = true;
          }
        }
      }

      alien.x -= Constants.ALIEN_SPEED * this.world.delta;

      if (alien.x <= -Constants.ALIEN_WIDTH) {
        this.aliens.splice(i, 1);
      }
    }
  }
}
