import Constants from './Constants';

export default class PlayerState {
  action: string = 'run';
  state: string = 'A';
  stateTime = 0;
  states = ['A', 'B', 'B', 'C', 'C', 'D'];
  stateIndex = 0;
  transitionTime = 0;
  stunnedDuration = 0;
  appliedStun = false;
  hp = 3;
  score = 0;
  complete = false;
  getCurrentStateIndex(): number {
    return this.stateIndex;
  }
  changeAction(action: string) {
    if (action === this.action) return;
    this.action = action;
    this.stateTime = 0;
  }
  changeState(state: string) {
    this.transitionTime = Constants.TRANSITION_DURATION;
    this.state = state;
  }
  nextState() {
    const index = this.stateIndex + 1;
    if (index >= this.states.length) {
      this.complete = true;
      return;
    }
    this.stateIndex = index;
    this.changeState(this.states[index]);
  }
  process(delta: number) {
    this.stateTime += delta;
    if (this.transitionTime > 0) {
      this.transitionTime -= delta;
    }
  }
}
