import { Inject, System } from 'flat-ecs';
import {
  createAnimation,
  createBatch,
  createWhiteTexture,
  SpriteBatch,
  Texture,
  TextureAtlas,
  Vector2,
  Viewport,
  Animation,
  PlayMode,
  TextureRegion
} from 'gdxjs';
import AssetManager from '../AssetManager';
import Constants from '../Constants';
import PlayerState from '../PlayerState';

export default class PlayerRenderSystem extends System {
  @Inject('viewport')
  viewport: Viewport;
  @Inject('context')
  gl: WebGLRenderingContext;
  @Inject('playerPosition')
  playerPos: Vector2;
  @Inject('assetManager')
  assetManager: AssetManager;
  @Inject('playerState')
  playerState: PlayerState;
  batch: SpriteBatch;
  whiteTex: Texture;
  atlas: TextureAtlas;
  animations: any = {};
  initialized() {
    this.batch = createBatch(this.gl);
    this.whiteTex = createWhiteTexture(this.gl);

    const atlas = this.assetManager.getAtlas('./main-char.atlas');
    if (atlas) {
      this.atlas = atlas;
      this.animations['upgrade'] = createAnimation(0.08, atlas.findRegions('upgrade_blast'));
    }
  }
  animationFrameDuration: any = {
    run: 0.1,
    jump: 0.1,
    doubleJump: 0.1,
    hurt: 0.05
  };
  getAnimation(action: string, state: string): Animation {
    const animName = `${action}${state}`;
    if (!this.animations[animName]) {
      const frameDuration: number = this.animationFrameDuration[action] || 0.1;
      let frames = this.atlas.findRegions(animName);
      if (action === 'jump' || action === 'doubleJump') {
        frames = frames.slice(0, frames.length - 2) as [TextureRegion];
      }
      this.animations[animName] = createAnimation(frameDuration, frames);
    }
    return this.animations[animName];
  }
  process(): void {
    this.batch.setProjection(this.viewport.getCamera().combined);
    this.batch.begin();
    const { x, y } = this.playerPos;
    this.playerState.process(this.world.delta);

    let playMode = PlayMode.LOOP;
    if (this.playerState.action === 'jump' || this.playerState.action === 'doubleJump') {
      playMode = PlayMode.NORMAL;
    }

    const animation = this.getAnimation(this.playerState.action, this.playerState.state);
    animation
      .getKeyFrame(this.playerState.stateTime, playMode)
      .draw(
        this.batch,
        x - Constants.PLAYER_WIDTH / 2,
        y - Constants.PLAYER_HEIGHT / 2,
        Constants.PLAYER_WIDTH,
        Constants.PLAYER_HEIGHT,
        Constants.PLAYER_WIDTH / 2,
        Constants.PLAYER_HEIGHT,
        0,
        Constants.PLAYER_SCALE,
        Constants.PLAYER_SCALE
      );

    if (this.playerState.transitionTime > 0) {
      const blast = this.animations['upgrade'] as Animation;
      blast
        .getKeyFrame(this.playerState.stateTime, PlayMode.LOOP)
        .draw(
          this.batch,
          x - Constants.PLAYER_WIDTH / 2,
          y,
          Constants.PLAYER_WIDTH,
          Constants.PLAYER_HEIGHT,
          Constants.PLAYER_WIDTH / 2,
          Constants.PLAYER_HEIGHT,
          0,
          Constants.PLAYER_SCALE * 1.2,
          Constants.PLAYER_SCALE * 1.2
        );
    }
    this.batch.end();
  }
}
