import { b2Contact, b2World } from 'box2d.ts';
import { EventEmitter, EventSubscription } from 'fbemitter';
import { Inject, System } from 'flat-ecs';
import PlayerState from '../PlayerState';
import Star from '../Star';

export default class StarCollisionSystem extends System {
  @Inject('physicsWorld')
  physicsWorld: b2World;
  @Inject('stars')
  stars: Array<Star>;
  @Inject('eventEmitter')
  eventEmitter: EventEmitter;
  @Inject('playerState')
  playerState: PlayerState;
  subscription: EventSubscription;
  initialized() {
    this.subscription = this.eventEmitter.addListener('beginContact', (contact: b2Contact) => {
      let otherFixture, starFixture;
      const fixA = contact.GetFixtureA();
      const fixB = contact.GetFixtureB();
      if (fixA.GetBody().GetUserData()?.type === 'star') {
        otherFixture = fixB;
        starFixture = fixA;
      } else if (fixA.GetBody().GetUserData()?.type === 'star') {
        otherFixture = fixA;
        starFixture = fixB;
      }
      if (starFixture && otherFixture && otherFixture.GetBody().GetUserData()?.type === 'user') {
        this.removeStar(starFixture.GetBody().GetUserData().starObject);
      }
    });
  }
  process(): void {}
  removeStar(starObject: any) {
    if (!starObject.active) {
      return;
    }
    if (starObject.special) {
      this.playerState.nextState();
    } else {
      this.playerState.score += 100;
    }
    starObject.active = false;
  }
  dispose() {
    this.subscription.remove();
  }
}
