import {
  createGameLoop,
  createOrthoCamera,
  createStage,
  createViewport,
  createViewportAwareInputHandler,
  Vector2
} from 'gdxjs';
import { b2World } from 'box2d.ts';
import { World } from 'flat-ecs';
import './index.css';
import Constants from './Constants';
import PhysicsSystem from './systems/PhysicsSystem';
import CameraMovementSystem from './systems/CameraMovementSystem';
import BlockGenerationSystem from './systems/BlockGenerationSystem';
import BlockRenderSystem from './systems/BlockRenderSystem';
import PlayerMovementSystem from './systems/PlayerMovementSystem';
import PlayerRenderSystem from './systems/PlayerRenderSystem';
import BlockRemovalSystem from './systems/BlockRemovalSystem';
import AssetManager from './AssetManager';
import PlayerState from './PlayerState';
import BackgroundRenderSystem from './systems/BackgroundRenderSystem';
import SmokeRenderSystem from './systems/SmokeRenderSystem';

import { EventEmitter } from 'fbemitter';
import Block from './Block';
import Star from './Star';
import StarRenderSystem from './systems/StarRenderSystem';
import StarCollisionSystem from './systems/StarCollisionSystem';
import Alien from './Alien';
import AlienSystem from './systems/AlienSystem';
import AlienRenderSystem from './systems/AlienRenderSystem';
import UISystem from './systems/UISystem';

const init = async () => {
  const eventEmitter = new EventEmitter();
  const stage = createStage();
  const canvas = stage.getCanvas();
  const info = stage.getInfo();

  const viewport = createViewport(canvas, Constants.CAMERA_WIDTH, Constants.CAMERA_HEIGHT, {
    pixelRatio: 1
  });
  const gl = viewport.getContext();
  const inputHandler = createViewportAwareInputHandler(canvas, viewport);

  const fixedCamera = createOrthoCamera(Constants.CAMERA_WIDTH, Constants.CAMERA_HEIGHT, canvas.width, canvas.height);
  viewport.addCamera(fixedCamera);
  viewport.update();

  const assetManager = new AssetManager(gl);
  assetManager.loadAtlas('./main-char.atlas');
  assetManager.loadAtlas('./ground.atlas');
  assetManager.loadAtlas('./object.atlas');
  ['bg', 'city_01', 'city_02', 'cloud_01', 'cloud_02', 'smoke_01', 'smoke_02'].map(s =>
    assetManager.loadTexture(`./background/${s}.png`)
  );
  assetManager.loadAtlas('./ui.atlas');
  assetManager.loadFont('./score.fnt');
  info.innerHTML = `Loading...`;
  await assetManager.finishLoading();

  const createNewWorld = () => {
    const blocks: Array<Block> = [];
    const stars: Array<Star> = [];
    const aliens: Array<Alien> = [];
    const physicsWorld = new b2World({ x: 0, y: Constants.GRAVITY });
    const gameWorld = new World();
    const playerState = new PlayerState();
    const playerPosition = new Vector2(0, 0);
    viewport.getCamera().setPosition(0, 0);

    gameWorld.register('context', gl);
    gameWorld.register('physicsWorld', physicsWorld);
    gameWorld.register('viewport', viewport);
    gameWorld.register('blocks', blocks);
    gameWorld.register('stars', stars);
    gameWorld.register('aliens', aliens);
    gameWorld.register('playerPosition', playerPosition);
    gameWorld.register('inputHandler', inputHandler);
    gameWorld.register('assetManager', assetManager);
    gameWorld.register('playerState', playerState);
    gameWorld.register('fixedCamera', fixedCamera);
    gameWorld.register('eventEmitter', eventEmitter);

    gameWorld.addSystem(new BlockGenerationSystem());
    gameWorld.addSystem(new PlayerMovementSystem());
    gameWorld.addSystem(new BlockRemovalSystem());
    gameWorld.addSystem(new StarCollisionSystem());
    gameWorld.addSystem(new AlienSystem());
    gameWorld.addSystem(new PhysicsSystem());

    gameWorld.addSystem(new CameraMovementSystem(), false);
    gameWorld.addSystem(new BackgroundRenderSystem(), false);
    gameWorld.addSystem(new BlockRenderSystem(), false);
    gameWorld.addSystem(new StarRenderSystem(), false);
    gameWorld.addSystem(new AlienRenderSystem(), false);
    gameWorld.addSystem(new PlayerRenderSystem(), false);
    gameWorld.addSystem(new SmokeRenderSystem(), false);
    gameWorld.addSystem(new UISystem(), false);
    return gameWorld;
  };

  let gameWorld = createNewWorld();
  let gameOver = false;

  eventEmitter.addListener('gameOver', () => {
    gameOver = true;
    gameWorld.dispose();
    setTimeout(() => {
      gameWorld = createNewWorld();
      gameOver = false;
    }, 100);
  });

  gl.clearColor(0, 0, 0, 1);
  const loop = createGameLoop(delta => {
    gl.clear(gl.COLOR_BUFFER_BIT);
    gameWorld.setDelta(delta);
    !gameOver && gameWorld.processActiveSystem();
    gameWorld.processPassiveSystem();
  });

  setInterval(() => (info.innerHTML = `FPS: ${loop.getFps()}`), 500);
};

init();
