import { BitmapFont, loadAtlas, loadFont, loadTexture, Texture, TextureAtlas } from 'gdxjs';

export default class AssetManager {
  gl: WebGLRenderingContext;
  promises: Array<Promise<any>> = [];
  atlases: Map<string, TextureAtlas> = new Map();
  textures: Map<string, Texture> = new Map();
  done = 0;
  constructor(gl: WebGLRenderingContext) {
    this.gl = gl;
  }
  loadAtlas(path: string): void {
    const promise = loadAtlas(this.gl, path, {}).then(atlas => {
      this.atlases.set(path, atlas);
      this.done++;
    });
    this.promises.push(promise);
  }
  fonts: Map<string, BitmapFont> = new Map();
  loadFont(path: string): void {
    const promise = loadFont(this.gl, path, {}).then(font => {
      this.fonts.set(path, font);
      this.done++;
    });
    this.promises.push(promise);
  }
  getFont(path: string): BitmapFont | undefined {
    return this.fonts.get(path);
  }
  getAtlas(path: string): TextureAtlas | undefined {
    return this.atlases.get(path);
  }
  loadTexture(path: string): void {
    const promise = loadTexture(this.gl, path, {}).then(atlas => {
      this.textures.set(path, atlas);
      this.done++;
    });
    this.promises.push(promise);
  }
  getTexture(path: string): Texture | undefined {
    return this.textures.get(path);
  }
  async finishLoading() {
    await Promise.all(this.promises);
  }
  getDone(): number {
    return this.done;
  }
  getTotal(): number {
    return this.promises.length;
  }
}
